<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-evaluate-infor">
      <div class="personnal-detail-wrap-evaluate-infor-title">
        <b>{{EVALPName}}</b>
      </div>
      <div class="personnal-detail-wrap-evaluate-infor-content">
        <div class="personnal-detail-wrap-evaluate-infor-content-left">
          <div class="personnal-detail-wrap-evaluate-infor-content-left-item">{{evaltypename}}</div>
          <div class="personnal-detail-wrap-evaluate-infor-content-left-item">{{evalobj}}</div>
          <!-- <div class="personnal-detail-wrap-evaluate-infor-content-left-item">课程名称：{{lesson}}</div> -->
          <div
            class="personnal-detail-wrap-evaluate-infor-content-left-item"
          >评价时间范围：{{stardate}} -- {{enddate}}</div>
        </div>
        <div class="personnal-detail-wrap-evaluate-infor-content-right">
          <div class="personnal-detail-wrap-evaluate-infor-content-right-title">综合分值</div>
          <div class="personnal-detail-wrap-evaluate-infor-content-right-score">
            <div
              class="personnal-detail-wrap-evaluate-infor-content-right-score-total"
            >{{totalPoint}}</div>
            <div
              class="personnal-detail-wrap-evaluate-infor-content-right-score-hightest"
            >{{maxPoint}}</div>
            <div class="personnal-detail-wrap-evaluate-infor-content-right-score-aver">{{avePoint}}</div>
            <div
              class="personnal-detail-wrap-evaluate-infor-content-right-score-lowest"
            >{{minPoint}}</div>
          </div>
          <div class="personnal-detail-wrap-evaluate-infor-content-right-content">
            <div class="personnal-detail-wrap-evaluate-infor-content-right-content-hightest">总分值</div>
            <div class="personnal-detail-wrap-evaluate-infor-content-right-content-hightest">最高分</div>
            <div class="personnal-detail-wrap-evaluate-infor-content-right-content-aver">平均分</div>
            <div class="personnal-detail-wrap-evaluate-infor-content-right-content-lowest">最低分</div>
          </div>
        </div>
      </div>
    </div>
    <el-tabs class="personnal-detail-wrap-tabs" v-model="activeName">
      <el-tab-pane label="评分细则" name="first">
        <div class="ed-left-wrap-bottom">
          <div v-if="isZero">目前暂无评分 ~_~!!</div>
          <div class="ed-scroll-box">
            <div
              class="ed-detail-item"
              v-for="(item, j) in details"
              :key="j"
              :class="{'n-b-r': (j+1) % 6 == 0}"
            >
              <div class="ed-detail-item-l">
                <p>{{item.cstitle}}</p>
                <p>{{item.lessonsTime}}</p>
              </div>
              <div class="ed-detail-item-r">
                <div class="ed-detail-item-value">{{item.value}}</div>
                <div class="ed-detail-item-tname">老师{{j+1}}</div>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <!-- <el-tab-pane label="课堂评语" name="second">
        <div class="ed-right-wrap">
          <div v-if="isComment">目前暂无评语 ~_~!!</div>
          <div class="ed-scroll-box">
            <div class="ed-bar-item" v-for="(item, index) in comments" :key="index">
              <div
                class="ed-bar-item-avater"
                :class="{'eval-boy-img':(item.gender=='1')&&(item.avater==''),'eval-girl-img':(item.gender=='2')&&(item.avater=='')}"
              >
                <img v-if="item.avater != ''" :src="item.avater">
              </div>
              <div class="ed-bar-item-detail">
                <div class="ed-bar-item-detail-head">
                  <span class="fl">老师{{index+1}}</span>
                  <span class="fr">{{item.date}}</span>
                  <div class="clear"></div>
                </div>
                <div class="ed-bar-item-detail-content">{{item.content}}</div>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>-->
    </el-tabs>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import comment from "../../../../components/course/detailComment";
export default {
  components: {
    comment
  },
  data() {
    return {
      hasrecid: true,
      activeName: "first",
      dept: "",
      EVALPID: "",
      EVALPName: "",
      teacherName: "",
      date: "",
      lesson: "",
      details: [],
      comments: [],
      totalPoint: 0,
      maxPoint: 0,
      avePoint: 0,
      minPoint: 0,
      evaltypename: "",
      evalobj: "",
      evlapType: "",
      stardate: "",
      enddate: "",
      isZero: false,
      isComment: false
    };
  },
  mounted() {
    this.lesson = this.$route.query.title;
    this.teacherName = this.$route.query.teacher;
    this.date = this.$route.query.csDate;
    this.dept = this.$route.query.dept;
    this.EVALPID = this.$route.query.evalpid;
    this.EVALPName = this.$route.query.evalpname;
    this.evaltypename = this.$route.query.evaltypename;
    this.evalobj = this.$route.query.evalobj;
    this.evlapType = this.$route.query.evlapType;
    this.stardate = this.$route.query.stardate;
    this.enddate = this.$route.query.enddate;
    this.init();
  },
  methods: {
    async init() {
      this.isZero = false;
      this.isComment = false;
      let res = await http.get(api.GetRatingFormDataByEPID, {
        id: this.EVALPID,
        token: http.getToken()
      });
      if (res.Result) {
        let totalTemp = 0;
        let maxTemp = 0;
        let minTemp = 10000;
        let sumTemp = 0;
        totalTemp = parseFloat(res.Datas[0].tScore).toFixed(1);
        let downloadURL = await api.GetDownloadFileURL();
        res.Datas.forEach(element => {
          let s = element.Score * 10;
          sumTemp += s;
          if (s > maxTemp) {
            maxTemp = s;
          }
          if (s < minTemp) {
            minTemp = s;
          }
          let imgURL = "";
          if (element.TeacherPhoto != "") {
            imgURL =
              downloadURL +
              "?filename=" +
              element.TeacherPhoto +
              "&fileType=1&platFormType=1";
          }
          if (element.Comment != "") {
            this.comments.push({
              name: element.TeacherName,
              avater: imgURL,
              date: element.CreateDate,
              content: element.Comment,
              gender: element.TeacherGender
            });
          }
          this.details.push({
            cstitle: element.cstTitle,
            rftime: element.rftime,
            lessonsTime: element.lessonsTime,
            name: element.TeacherName,
            value: parseFloat(element.Score).toFixed(1)
          });
        });
        this.totalPoint = totalTemp;
        this.maxPoint = parseFloat(maxTemp / 10).toFixed(1);
        this.minPoint = parseFloat(minTemp / 10).toFixed(1);
        this.avePoint = (sumTemp / parseInt(res.RowsCount) / 10).toFixed(1);
      }
      if (this.comments.length == 0) {
        this.isZero = true;
      }
      if (this.details.length == 0) {
        this.isComment = true;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.personnal-detail-wrap {
  &-evaluate-infor {
    height: 215px;
    margin-left: 20px;
    margin-bottom: 12px;
    &-title {
      height: 42px;
      text-align: left;
      font-size: 1.4rem;
    }
    &-content {
      height: 170px;
      &-left {
        width: 40%;
        float: left;
        &-item {
          text-align: left;
          height: 20px;
          margin-top: 20px;
        }
      }
      &-right {
        width: 53%;
        float: right;
        &-title {
          text-align: left;
          height: 20px;
          margin-top: 20px;
        }
        &-score {
          height: 77px;
          div {
            height: 100%;
            width: 20%;
            float: left;
            display: block;
            text-align: center;
            margin: 10px 10px 10px 0px;
            font-size: 3rem;
            border-bottom: 1px solid rgb(189, 186, 186);
          }
          &-total {
            color: darkcyan;
          }
          &-hightest {
            color: #3faf0b;
          }
          &-aver {
            color: #2a9efc;
          }
          &-lowest {
            color: #f47575;
          }
        }
        &-content {
          height: 25px;
          div {
            height: 100%;
            width: 20%;
            float: left;
            display: block;
            text-align: center;
            margin: 0 10px 0 0px;
            font-size: 1rem;
            color: gray;
          }
        }
      }
    }
  }
  .evaluated-card-Content {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    .evaluated-card {
      width: 130px;
      height: 100px;
      float: left;
      background: #f6f6f6;
      justify-content: space-around;
      align-items: center;
      position: relative;
      margin-right: 20px;
      margin-top: 20px;
      &-score {
        height: 65px;
        font-size: 2.8rem;
        margin: 0 10px 0 10px;
        border-bottom: 1px solid rgb(189, 186, 186);
      }
      &-teacher {
        text-align: center;
        margin-top: 5px;
      }
    }
  }
  &-tabs {
    .ed-left-wrap {
      &-bottom {
        width: 100%;
        height: calc(100% - 166px);
        .ed-scroll-box {
          width: 100%;
          height: calc(100% - 20px);
          overflow-x: hidden;
          overflow-y: scroll;
          .ed-detail-item {
            width: 300px;
            height: 88px;
            padding: 17px 20px;
            background: #f6f6f6;
            float: left;
            margin-top: 10px;
            margin-bottom: 10px;
            user-select: none;
            text-align: left;
            .ed-detail-item-l {
              float: left;
              width: 180px;
              p {
                font-size: 18px;
                padding: 0px;
                margin: auto;
                line-height: 30px;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
              }
              p:last-child {
                font-size: 14px;
                color: #999;
              }
            }
            .ed-detail-item-r {
              text-align: center;
              float: left;
              width: 80px;
              margin-top: -12px;
            }
            &-value {
              width: 100%;
              color: #368bfe;
              float: left;
              font-size: 40px;
              font-weight: 500;
            }
            &-time {
              position: absolute;
              float: left;
              margin-top: 30px;
              // border-top: 1px solid #e7e7e7;
              padding-top: 8px;
              color: #999;
            }
          }
          .n-b-r {
            margin-right: 0 !important;
          }
        }
      }
    }
    .ed-right-wrap {
      .ed-bar-item {
        width: 100%;
        padding: 15px;
        padding-left: 8px;
        background: #f6f6f6;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 15px;
        &-avater {
          width: 60px;
          height: 60px;
          border: 4px solid #fff;
          box-shadow: 0 0 6px #ccc;
          overflow: hidden;
          border-radius: 50%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        &-detail {
          width: calc(100% - 77px);
          &-head {
            width: 100%;
            font-size: 0.8rem;
            color: #999;
          }
          &-content {
            width: 100%;
            text-align: left;
            font-size: 0.9rem;
            margin-top: 5px;
          }
        }
      }
    }
  }
  .eval-boy-img {
    background: url("../../../../assets/boy_img.png");
    background-size: 55px 55px;
    background-repeat: no-repeat;
  }
  .eval-girl-img {
    background: url("../../../../assets/girl_img.png");
    background-size: 55px 55px;
    background-repeat: no-repeat;
  }
}
</style>



